<template>
  <div class="joinNumber">
    <form action v-on:submit="chkForm">
      <div class="descBox">
        <p>서비스 이용 시 휴대폰인증이 필요합니다.</p>
        <div class="inputBox">
          <input type="text" v-model="keyNumber" maxlength="6" />
          <div class="timer">
            <span>{{ timeValue}}</span>
          </div>
        </div>

        <div class="reSendBtn">
          <span>재전송</span>
        </div>
      </div>
      <div class="btnArea">
        <div class="leftBtn">
          <a href>이전</a>
        </div>
        <div class="rightBtn">
          <button type="submit">확인</button>
        </div>
      </div>
      <info-text></info-text>
    </form>
  </div>
</template>

<script>
import InfoText from '@/components/Join/InfoText.vue';
export default {
	data() {
		return {
			keyNumber: '',
			timeValue: '3:00',
		};
	},
	components: {
		InfoText,
	},
	methods: {
		chkForm() {
			if (this.keyNumber.length < 6) {
				alert('인증번호를 재확인 해주시기 바랍니다.');
				return false;
			}
		},
		startTimer(duration) {
			var timer = 60 * 3,
				minutes,
				seconds;
			var vd = this;
			setInterval(function() {
				minutes = parseInt(timer / 60, 10);
				seconds = parseInt(timer % 60, 10);

				minutes = minutes < 10 ? minutes : minutes;
				seconds = seconds < 10 ? '0' + seconds : seconds;

				vd.timeValue = minutes + ':' + seconds;

				if (--timer < 0) {
					timer = duration;
				}
			}, 1000);
		},
	},

	created() {
		this.startTimer();
	},
};
</script>

